"use client";

import { useRouter } from "next/navigation";
import { Project } from "@/types/Project";

interface ProjectListProps {
  projects: Project[];
}

export default function ProjectList({ projects }: ProjectListProps) {
  const router = useRouter();

  const handleProjectClick = (slug: string) => {
    router.push(`/projects/${slug}`);
  };

  const getStatusLabel = (status: string) => {
    switch (status) {
      case "beta-test":
        return "Beta";
      case "in-progress":
        return "In Progress";
      case "completed":
        return "Live";
      case "planned":
        return "Planned";
      default:
        return status;
    }
  };

  return (
    <div className="w-full">
      {projects.map((project, index) => (
        <div
          key={project.id}
          onClick={() => handleProjectClick(project.slug)}
          className="group cursor-pointer border-b border-gray-800 py-6 md:py-8 transition-all duration-300 hover:border-gray-600"
          style={{
            animation: `fadeSlideIn 0.5s ease-out ${index * 0.1}s both`,
          }}
        >
          {/* Project Title - Large Typography */}
          <div className="flex flex-col md:flex-row md:items-baseline md:justify-between gap-2 md:gap-8">
            <h2 className="text-3xl md:text-5xl lg:text-6xl font-light text-gray-300 group-hover:text-white transition-colors duration-300 tracking-tight">
              {project.title}
            </h2>

            {/* Meta Info */}
            <div className="flex items-center gap-4 text-sm text-gray-500 group-hover:text-gray-400 transition-colors">
              <span>{getStatusLabel(project.status)}</span>
              <span className="hidden md:inline">/</span>
              <span className="hidden md:inline">
                {project.techStack?.slice(0, 2).join(", ")}
              </span>
            </div>
          </div>

          {/* Hover Arrow */}
          <div className="mt-2 overflow-hidden h-0 group-hover:h-6 transition-all duration-300">
            <span className="text-gray-500 text-sm flex items-center gap-2">
              View Project
              <svg
                className="w-4 h-4 transform group-hover:translate-x-1 transition-transform"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M17 8l4 4m0 0l-4 4m4-4H3"
                />
              </svg>
            </span>
          </div>
        </div>
      ))}
    </div>
  );
}
